import React, { Component } from "react";
import ScrollAnimation from "react-animate-on-scroll";
import { withFormik } from "formik";
import superagent from "superagent";
import ReactGA from "react-ga";
import "./Contact.scss";
let HOST_AUTH = "https://curbmap.com";

const ContactForm = ({
  values,
  errors,
  touched,
  handleChange,
  handleBlur,
  handleReset,
  handleSubmit,
  isSubmitting
}) => (
  <form onSubmit={handleSubmit}>
    E-mail:<br />
    <input
      type="text"
      name="email"
      placeholder="email"
      autoCapitalize="none"
      autoCorrect={values.email.toString() ? values.email : undefined}
      onChange={handleChange}
      onBlur={handleBlur}
      value={values.email}
    />
    {touched.email &&
      errors.email && <div className="error-label">{errors.email}</div>}
    {(!touched.email || !errors.email) && <br />}
    Name:<br />
    <input
      type="text"
      name="name"
      placeholder="name"
      autoComplete="off"
      autoCapitalize="word"
      autoCorrect={values.name.toString() ? values.name : undefined}
      onChange={handleChange}
      onBlur={handleBlur}
      value={values.name}
    />
    {touched.name &&
      errors.name && <div className="error-label">{errors.name}</div>}
    {(!touched.name || !errors.name) && <br />}
    We value your opinion: <br />
    <textarea
      name="textarea"
      placeholder="Let us know what you're thinking"
      autoComplete="on"
      autoCapitalize="off"
      autoCorrect={values.textarea.toString() ? values.textarea : undefined}
      onChange={handleChange}
      onBlur={handleBlur}
      value={values.textarea}
    />
    {touched.textarea &&
      errors.textarea && <div className="error-label">{errors.textarea}</div>}
    {(!touched.textarea || !errors.textarea) && <br />}
    {isSubmitting && (
      <button
        type="submit"
        disabled={isSubmitting}
        className="isSubmitting"
      >
        Send
      </button>
    )}
    {!isSubmitting && (
      <button
        type="submit"
        disabled={isSubmitting}
      >
        Send
      </button>
    )}
    <button className="reset" onClick={handleReset}>
      Reset
    </button>
  </form>
);
const FormikContactForm = withFormik({
  // Transform outer props into form values
  mapPropsToValues: props => ({ email: "", name: "", textarea: "" }),
  validate: (values, props) => {
    const errors = {};
    if (!values.email) {
      errors.email = "Required";
    } else if (
      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
    ) {
      errors.email = "Invalid email address";
    }
    if (!values.name) {
      errors.name =
        "Required, because we really want to know what to call you.";
    }
    if (!values.textarea) {
      errors.textarea = "We hope you have something you'd like us to know.";
    }
    return errors;
  },
  handleSubmit: (values, props) => {
    props.props.handleSubmit(values, props);
  },
  resetForm: nextvalues => {}
})(ContactForm);

class Contact extends Component {
  constructor(props) {
    super(props);
    this.formHandler = this.formHandler.bind(this);
    this.scrollUp = this.scrollUp.bind(this);
    this.state = {
      email: "",
      name: "",
      textarea: "",
      showForm: true,
      warning: null,
      isSubmitting: null
    };
  }
  formHandler(evt, props) {
    this.setState({
      showForm: false,
      name: evt.name,
      email: evt.email,
      textarea: evt.textarea,
      isSubmitting: "isSubmitting"
    });
    console.log(props);
    superagent
      .post(HOST_AUTH + "/submitContact")
      .send({ email: evt.email, name: evt.name, text: evt.textarea })
      .set("Content-Type", "application/x-www-form-urlencoded")
      .end((err, res) => {
        if (err) {
          this.setState({
            warning:
              "Something went wrong on the server. If this persists, let me know at: eli.j.selkin@gmail.com"
          });
          return;
        }
        if (res.body.success === 1) {
          props.resetForm();
          props.setSubmitting(false);
          ReactGA.initialize("UA-116738399-1");
          // Identify when someone has submitted... extra checking and graphs
          ReactGA.pageview(window.location.pathname + window.location.search);      
        } else {
          this.setState({
            warning:
              "Something was wrong with the form we were trying to send... check your form fields and make sure that they are all filled in."
          });
        }
      });
  }

  scrollUp(evt) {
    if (this.contactScroll) {
      this.contactScroll.scrollTop = 0;
    }
    this.setState({ showForm: true, warning: null, isSubmitting: null });
  }
  contactScroll = null;
  render() {
    return (
      <div className="ContactHolder">
        <ScrollAnimation
          animateIn="fadeIn"
          animateOut="fadeOut"
          animateOnce={true}
          className="ContactHeaderAnimation"
        >
          <div className="ContactHeaderHolder">
            <div className="ContactHeader">{"Hello! Let us hear from you"}</div>
          </div>
        </ScrollAnimation>
        <ScrollAnimation
          animateIn="swing"
          animateOut="bounceOutRight"
          className="ContactTextAnimation"
          afterAnimatedOut={this.scrollUp}
          duration={0.5}
        >
          <div className="Contact">
            {this.state.showForm && (
              <div
                className="ContactScroll"
                ref={ref => {
                  this.contactScroll = ref;
                }}
              >
                <div className="ContactName">Contact form</div>
                <FormikContactForm handleSubmit={this.formHandler} />
                {this.state.warning && (
                  <div className="warning">{this.state.warning}</div>
                )}
              </div>
            )}
            {!this.state.showForm && (
              <div className="ContactScroll">
                <div className="ContactName">
                  Thanks for your input {this.state.name}. We'll get back to you
                  as soon as we can!
                </div>
              </div>
            )}
          </div>
        </ScrollAnimation>
      </div>
    );
  }
}

export default Contact;



